<template>
    <main class="mt-3">
        <div class="container">
            <h2 class="text-center">제품 수정</h2>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label">제품명</label>
                <div class="col-md-9">
                    <input type="text" class="form-control" v-model="productDetail.product_name" />
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label">제품가격</label>
                <div class="col-md-9">
                    <div class="input-group mb-3">
                        <input type="number" class="form-control" v-model="productDetail.product_price" />
                        <span class="input-group-text">원</span>
                    </div>
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label">배송비</label>
                <div class="col-md-9">
                    <div class="input-group mb-3">
                        <input type="number" class="form-control" v-model="productDetail.delivery_price" />
                        <span class="input-group-text">원</span>
                    </div>
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label">추가배송비(도서산간)</label>
                <div class="col-md-9">
                    <div class="input-group mb-3">
                        <input type="number" class="form-control" v-model="productDetail.add_delivery_price" />
                        <span class="input-group-text">원</span>
                    </div>
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label">제품카테고리</label>
                <div class="col-md-9">
                    <div class="row">
                        <div class="col-auto">
                            <select class="form-select" v-model="productDetail.category_id">
                                <option value="1">전자제품</option>
                                <option value="2">컴퓨터</option>
                                <option value="3">악세사리</option>
                            </select>
                        </div>
                    </div>
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label">태그</label>
                <div class="col-md-9">
                    <input type="text" class="form-control" v-model="productDetail.tags" />
                </div>
            </div>
            <div class="mb-3 row">
                <label class="col-md-3 col-form-label">출고일</label>
                <div class="col-md-9">
                    <div class="input-group mb-3">
                        <input type="number" class="form-control" v-model="productDetail.outbound_days" />
                        <span class="input-group-text">일 이내 출고</span>
                    </div>
                </div>
            </div>
            <div class="mb-3 row">
                <div class="col-6 d-grid p-1">
                    <button type="button" class="btn btn-lg btn-dark" @click="goToList">취소하기</button>
                </div>
                <div class="col-6 d-grid p-1">
                    <button type="button" class="btn btn-lg btn-danger" @click="saveProduct">저장하기</button>
                </div>
            </div>
        </div>
    </main>
</template>

<script>
import axios from 'axios';

export default {
    data() {
        return {
            productId: 0,
            productDetail: {
                product_name: '',
                product_price: 0,
                delivery_price: 0,
                add_delivery_price: 0,
                tags: '',
                outbound_days: 0,
                category_id: 1,
                id: null
            }
        };
    },
    computed: {
        user() {
            return this.$store.state.user;
        }
    },
    mounted() {
        if (this.user.profile == undefined) {
            alert('로그인을 해야 이용할 수 있습니다.');
            this.$router.push({ path: '/' });
        }
    },
    created() {
        this.productId = this.$route.query.product_id;
        this.getProductDetail();
    },
    methods: {
        goToList() {
            this.$router.push({ path: '/sales' });
        },
        async getProductDetail() {
            let productDetail = await this.$api('/api/productDetail', { param: [this.productId] });
            if (productDetail.length > 0) {
                this.productDetail = productDetail[0];
            }
        },
        async saveProduct() {
            try {
                // Axios를 사용하여 서버로 제품 정보 전송
                const response = await axios.post('/api/updateProduct', this.productDetail);
                console.log('Update successful:', response.data);
                alert('제품 정보가 성공적으로 저장되었습니다.');
                this.$router.push({ path: '/sales' }); // 저장 후 목록 페이지로 이동
            } catch (error) {
                console.error('제품 정보를 저장하는 중 오류가 발생했습니다:', error);
                alert('제품 정보를 저장하는 중 오류가 발생했습니다.');
            }
        }
    }
};
</script>
